.top-bar {
  margin-top: 32px;
  margin-bottom: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky; /* to keep cross always visible */
  top: 0;
  z-index: 1;

  &__logo {
    text-decoration: none;
    display: flex;
    width: 64px;
    height: 46px;

    @include hover(transform, scale(1.2));

    @include on-tablet {
      width: 87px;
      height: 65px;
    }
  }

  &__icons {
    display: flex;
    gap: 24px;
  }
}
